<template>
  <!--====== PART START ======-->
    
    <header  class="appie-header-area appie-sticky">
        <div class="container">
            <div class="header-nav-box">
                <div class="row align-items-center">
                    <div class="col-lg-2 col-md-4 col-sm-5 col-6 order-1 order-sm-1">
                        <div class="appie-logo-box">
                            <a href="/">
                                <img class="img-fluid bz-logo" src="@/assets/images/logo-inverted.png" alt="Basim">
                                <h4 class="bz-logo-text">Basim</h4>
                            </a>
                        </div>
                    </div>
                    <div class="col-lg-6 col-md-1 col-sm-1 order-3 order-sm-2">
                        <div class="appie-header-main-menu">
                            <nav-items :menuItems="menuItems" :nasted="nasted"/>
                        </div>
                    </div>
                    <div class="col-lg-4  col-md-7 col-sm-6 col-6 order-2 order-sm-3">
                        <div class="appie-btn-box" style="text-align:right">
                            <b-button v-b-modal.bz-modal class="main-btn ml-30">Get Started</b-button>
                            
                            <b-modal 
                            ref="modal" 
                            title="Contact Basim"
                            id="bz-modal"
                            @show="resetModal"
                            @hidden="resetModal"
                            @ok="handleOk"
                            >
                              <div class="d-block text-center">
                                <h4>Hello from Basim!</h4>
                                <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s.</p>
                              
                            </div>
                              
                                <form ref="form" @submit.stop.prevent="handleSubmit">
                                    <b-form-group
                                      label="Name"
                                      label-for="name-input"
                                      invalid-feedback="Name is required"
                                      :state="nameState"
                                    >
                                      <b-form-input
                                        id="name-input"
                                        v-model="name"
                                        :state="nameState"
                                        required
                                      ></b-form-input>
                                    </b-form-group>
                                    <b-form-group
                                      label="Email"
                                      label-for="email-input"
                                      invalid-feedback="Email is required"
                                      :state="emailState"
                                    >
                                      <b-form-input
                                        id="email-input"
                                        v-model="email"
                                        :state="emailState"
                                        required
                                      ></b-form-input>
                                    </b-form-group>
                                </form>
                            </b-modal>

                            <div class="toggle-btn ml-30 canvas_open d-lg-none d-block">
                                <i class="fa fa-bars" @click="showSidebar"></i>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    </header>
    
    <!--====== PART ENDS ======-->
</template>

<script>
import NavItems from '../NavItems.vue'
export default {
    data() {
      return {
        modalShow: false,
        name: '',
        nameState: null,
        email: '',
        emailState: null,
        submittedNames: []
      }
    },
    props:{
      menuItems:{
          type: Array,
          required: true,
      },
      nasted:{
            type:Boolean,
            default:true
        }
    },
	components: { NavItems },
     mounted() {
        document.addEventListener('scroll', this.stickMenu)
    },
    methods:{
        showSidebar(e){
            this.$emit("toggleSidebar",e)
        },
        stickMenu(){
        const result = document.querySelector('.appie-sticky')
           if (document.body.scrollTop > 100 || document.documentElement.scrollTop > 100) {
                result.classList.add('sticky')
            } else {
                result.classList.remove('sticky')
            }
        },

        // modal form
        checkFormValidity() {
        const valid = this.$refs.form.checkValidity()
        this.nameState = valid
        return valid
      },
      resetModal() {
        this.name = ''
        this.nameState = null
      },
      handleOk(bvModalEvent) {
        // Prevent modal from closing
        bvModalEvent.preventDefault()
        // Trigger submit handler
        this.handleSubmit()
      },
      handleSubmit() {
        // Exit when the form isn't valid
        if (!this.checkFormValidity()) {
          return
        }
        // Push the name to submitted names
        this.submittedNames.push(this.name)
        // Hide the modal manually
        this.$nextTick(() => {
          this.$bvModal.hide('modal-prevent-closing')
        })
    },
    }
}
</script>

